export default function getQuickSortAnimations(array){
  const a = [];

  quickSort(array,0,array.length-1,a);

  return a;
}

function quickSort(array,start,end,a)
{
  if(start<end)
  {
    let pindex = partition(array,start,end,a);

    quickSort(array,start,pindex-1,a);
    quickSort(array,pindex+1,end,a);
  }
}

function partition(array,start,end,a){

  let i=start+1;
  let j=end;
  let pivot = array[start];
  let temp;
  a.push([start,'p']);
  while(true)
  {

    while( i<=j && array[i]<=pivot)
    {
      a.push([i,j]);
      a.push([i,j,true]);
      i++;
    }
    while(i<=j && array[j]>=pivot )
    {
      a.push([i,j]);
      a.push([i,j,true]);
      j--;
    }
    if(i<=j)
    {
      temp = array[i];
      array[i] = array[j];
      array[j] = temp;
      a.push([i,j,array[i],array[j]]);
    }
    else{
      temp = array[start];
      array[start] = array[j];
      array[j] = temp;
      a.push([start,j,array[start],array[j]]);
      a.push([start,false]);
      return j;
    }

  }

}
