import React,{Component} from 'react';
import getMergeSortAnimations from './Algorithms/MergeSort';
import bubbleSortAnimations from './Algorithms/BubbleSort';
import getQuickSortAnimations from './Algorithms/QuickSort';
import getSelectionSortAnimations from './Algorithms/SelectionSort';
import './App.css';


class App extends Component{

     state = {
       array: [],
       bar_width:2,
       run:false,
       colors:["#51C5DD","red"],
       hover:[false,false,false,false,false],
       speed:10

     };

   componentDidMount() {

     this.generateArray();

   }


   generateArray = ()=> {
     const array = [];

     let val = document.querySelector('#size').value;

     if(val<=10)
     {
       this.setState({bar_width:30});
       this.setState({speed:60});
     }
     else if(val<=20 && val>10){
      this.setState({bar_width:20});
      this.setState({speed:35});
    }
    else if(val<=50 && val>20){
       this.setState({bar_width:10});
       this.setState({speed:30});
     }
    else if(val<=100 && val>50){
      this.setState({bar_width:7});
      this.setState({speed:25});
    }
    else if(val<=150 && val>100){
      this.setState({bar_width:5});
      this.setState({speed:20});
    }
    else if(val<=180 && val>150){
      this.setState({bar_width:4});
      this.setState({speed:10});
    }
    else{
      this.setState({bar_width:3});
      this.setState({speed:6});
    }



     for (let i = 0; i < val; i++) {
       array.push(this.randomIntFromInterval(5, 450));
     }
     this.setState({array});


   }

   changeTheme = ()=>
   {
     const color = document.getElementById("theme").value;
     if(color==="blue")this.setState({colors:["#51C5DD","red"]});
     else if(color==="orange")this.setState({colors:["orange","black"]});
     else if(color==="green")this.setState({colors:["#00A099","#EACB48"]});
     else if(color==="red")this.setState({colors:["#FF687E","#255D83"]});
   }

    randomIntFromInterval = (min, max)=> {


     return Math.floor(Math.random() * (max - min + 1) + min);
   }
   mouseOver1 = ()=>{
     this.setState({hover:[true,false,false,false,false]});

    }

    mouseOver2 = ()=>{
      this.setState({hover:[false,true,false,false,false]});
     }

     mouseOver3 = ()=>{
       this.setState({hover:[false,false,true,false,false]});
      }
      mouseOver4 = ()=>{
        this.setState({hover:[false,false,false,true,false]});
       }
       mouseOver5= ()=>{
         this.setState({hover:[false,false,false,false,true]});
        }
      mouseOut = ()=>{

        this.setState({hover:[false,false,false,false,false]});
      }

  selectionSort = ()=>{
    let t;
    let speed = this.state.speed;
    let ar = this.state.array.slice();
    this.setState({run:true});
    let animations = getSelectionSortAnimations(ar);

    for(let i=0;i<animations.length;i++)
    {
      let arrayBars = document.getElementsByClassName("element");
      const colorChange = animations[i].length===2 || animations[i].length === 3;
      if(colorChange)
      {
        const [barOneIdx,barTwoIdx] =[ animations[i][0],animations[i][1]];
        const barOneStyle = arrayBars[barOneIdx].style;
        const barTwoStyle = arrayBars[barTwoIdx].style;
        const color = animations[i].length===2?this.state.colors[1]:this.state.colors[0];

        setTimeout(()=>{barOneStyle.backgroundColor=color;
                          barTwoStyle.backgroundColor=color},i*speed);

      }
      else{
        const [barOneIdx,barTwoIdx,h1,h2] = animations[i];
        const barOneStyle = arrayBars[barOneIdx].style;
        const barTwoStyle = arrayBars[barTwoIdx].style;
        setTimeout(() => {

          barOneStyle.height = `${h1}px`;
          barTwoStyle.height = `${h2}px`;
        }, i*speed);
      }
      t=i+1;
    }
    setTimeout(()=>this.setState({run:false}),t*speed);
  }

  quickSort = ()=>{
    let t;
    let speed = this.state.speed;
    let ar = this.state.array.slice();
    this.setState({run:true});
    let animations = getQuickSortAnimations(ar);

    for(let i=0;i<animations.length;i++)
    {
      const arrayBars = document.getElementsByClassName('element');

        const colorChange = animations[i].length === 2 || animations[i].length===3;
        if(colorChange)
        {
          if(animations[i][1]==='p')
          {

            const barIndx = animations[i][0];
            const barStyle = arrayBars[barIndx].style;
            setTimeout(()=>{barStyle.backgroundColor="yellow";},i*speed);


          }
          else if(animations[i][1]===false)
          {
            const barIndx = animations[i][0];
            const barStyle = arrayBars[barIndx].style;
            setTimeout(()=>{barStyle.backgroundColor=this.state.colors[0];},i*speed);
          }
          else {
            const [barOneIdx, barTwoIdx] = animations[i];
            const barOneStyle = arrayBars[barOneIdx].style;
            const barTwoStyle = arrayBars[barTwoIdx].style;
            const color = animations[i].length===2 ? this.state.colors[1] : this.state.colors[0];
            setTimeout(() => {
              barOneStyle.backgroundColor = color;
              barTwoStyle.backgroundColor = color;
            }, i*speed);
          }

       }
       else{
         setTimeout(() => {
           const [barOneIdx,barTwoIdx,h1,h2] = animations[i];
           const barOneStyle = arrayBars[barOneIdx].style;
           const barTwoStyle = arrayBars[barTwoIdx].style;
           barOneStyle.height = `${h1}px`;
           barTwoStyle.height = `${h2}px`;
         }, i*speed);
       }
        t=i+1;
      }
      setTimeout(()=>this.setState({run:false}),t*speed);



  }

   mergeSort = ()=>{
     let t ;
     let speed = this.state.speed;
     let ar = this.state.array.slice();
     this.setState({run:true});
     const animations = getMergeSortAnimations(ar);

     for (let i = 0; i < animations.length; i++) {

       const arrayBars = document.getElementsByClassName('element');

       const isColorChange = i % 3 !== 2;
       if (isColorChange) {
         const [barOneIdx, barTwoIdx] = animations[i];
         const barOneStyle = arrayBars[barOneIdx].style;
         const barTwoStyle = arrayBars[barTwoIdx].style;
         const color = i % 3 === 0 ? this.state.colors[1] : this.state.colors[0];
         setTimeout(() => {
           barOneStyle.backgroundColor = color;
           barTwoStyle.backgroundColor = color;
         }, i*speed);
       } else {
         setTimeout(() => {
           const [barOneIdx, newHeight] = animations[i];
           const barOneStyle = arrayBars[barOneIdx].style;
           barOneStyle.height = `${newHeight}px`;
         }, i*speed);
       }
       t = i+1;
     }
     setTimeout(()=>this.setState({run:false}),t*speed);

   }

   bubbleSort = ()=> {
     let speed = this.state.speed;
     let t;
     this.setState({run:true});
     let ar = this.state.array.slice();
     const animations = bubbleSortAnimations(ar);

     for (let i = 0; i < animations.length; i++) {
       const arrayBars = document.getElementsByClassName('element');
       const isColorChange = i % 3 !== 2;
       if (isColorChange) {
         const [barOneIdx, barTwoIdx] = animations[i];
         const barOneStyle = arrayBars[barOneIdx].style;
         const barTwoStyle = arrayBars[barTwoIdx].style;
         const color = i % 3 === 0 ? this.state.colors[1] : this.state.colors[0];
         setTimeout(() => {
           barOneStyle.backgroundColor = color;
           barTwoStyle.backgroundColor = color;
         }, i*speed);
       } else {
         setTimeout(() => {

           const [barOneIdx,barTwoIdx,firstHeight,secondHeight] = animations[i];
           const barOneStyle = arrayBars[barOneIdx].style;
           const barTwoStyle = arrayBars[barTwoIdx].style;
           barOneStyle.height = `${firstHeight}px`;
           barTwoStyle.height = `${secondHeight}px`;
         }, i*speed);
       }
       t = i+1;
     }
     setTimeout(()=>this.setState({run:false}),t*speed);
   }




   render() {

     return (
      <div >

       <header style={{backgroundColor:`${this.state.colors[0]}`}}>

        <div>
       <label for="theme" style={{fontSize:`${20}px`}} >Theme:</label>
       <select name="theme" id="theme" style={{marginBottom:`${4}px`}} onChange={this.changeTheme}>

         <option value="blue">blue</option>
         <option value="orange">orange</option>
         <option value="green">green</option>
         <option value="red">red</option>

       </select>

          </div>

          <input className="slider" onChange={this.generateArray}  type="range" min="5" max="200" id='size'/>


          <nav>
           <ul>
           <li onMouseOut={this.mouseOut} onMouseOver={this.mouseOver1} style={{cursor:this.state.hover[0] && !this.state.run?"pointer":"not-allowed",color:this.state.hover[0] && !this.state.run?`${this.state.colors[0]}`:"white",backgroundColor:this.state.hover[0] && !this.state.run?"white":`${this.state.colors[0]}`}} onClick={this.state.run?"none":this.generateArray} id="newArray" >New Array</li>
           <div className="divider" ></div>

           <li onMouseOut={this.mouseOut} onMouseOver={this.mouseOver2} style={{cursor:this.state.hover[1] && !this.state.run ?"pointer":"not-allowed",color:this.state.hover[1] && !this.state.run?`${this.state.colors[0]}`:"white",backgroundColor:this.state.hover[1] && !this.state.run?"white":`${this.state.colors[0]}`}} onClick={this.state.run?"none":this.mergeSort} id="mergesort">MergeSort</li>
           <div className="divider" ></div>

           <li onMouseOut={this.mouseOut} onMouseOver={this.mouseOver3} style={{cursor:this.state.hover[2] && !this.state.run?"pointer":"not-allowed",color:this.state.hover[2] && !this.state.run?`${this.state.colors[0]}`:"white",backgroundColor:this.state.hover[2] && !this.state.run?"white":`${this.state.colors[0]}`}} onClick={this.state.run?"none":this.bubbleSort} id="bubblesort">BubbleSort</li>
           <div className="divider"></div>
           <li onMouseOut={this.mouseOut} onMouseOver={this.mouseOver4} style={{cursor:this.state.hover[3] && !this.state.run?"pointer":"not-allowed",color:this.state.hover[3] && !this.state.run?`${this.state.colors[0]}`:"white",backgroundColor:this.state.hover[3] && !this.state.run?"white":`${this.state.colors[0]}`}} onClick={this.state.run?"none":this.quickSort} id="quicksort" >QuickSort</li>
           <div className="divider"></div>
           <li onMouseOut={this.mouseOut} onMouseOver={this.mouseOver5} style={{cursor:this.state.hover[4] && !this.state.run?"pointer":"not-allowed",color:this.state.hover[4] && !this.state.run?`${this.state.colors[0]}`:"white",backgroundColor:this.state.hover[4] && !this.state.run?"white":`${this.state.colors[0]}`}} onClick={this.state.run?"none":this.selectionSort} id="quicksort" >SelectionSort</li>


           </ul>
         </nav>
        </header>

       <div className="Mcontainer" style={{position:"center"}}>

       <br></br><br></br><br></br><br></br>
         {this.state.array.map((value, idx) => (
           <div
             className="element"
             key={idx}
             style={{
               backgroundColor:`${this.state.colors[0]}`,
               width:`${this.state.bar_width}px`,
               height:`${value}px`
             }}></div>
         ))}

       </div>

       </div>
     );
   }
 }


export default App;
